/*Mobile screens tend to have things like
title bars, etc that shrink the size "seen" */

@media screen and ( max-height: 650px ){
	#canvasWrapper {
		height:64vh;
	}
	
	#outer-container {
		height:88vh;
		overflow:hidden;
	}
	
	#page-wrap { border:none; }

	
	.bm-menu {
	  padding-top: 0em!important;
	  padding-bottom:0em!important;
	}
	
	.InputBox { margin-top:10px; 
				font-size:.85em; }
	
	h3 {
		-webkit-margin-before: 0.5em;
		-webkit-margin-after: 0.5em;
	}
	
	.ResetButton {
		background-color: #4CAF50; /* Green */
		border: none;
		color: white;
		padding: 7px 15px;
		text-align: center;
		text-decoration: none;
		display: inline-block;
		font-size: 14px;
		border-radius: 5px;
		margin-top:10px;
	}
	
	
	html.touch body {
     overflow:hidden;
	}
	
	/*.referenceLink 
	{ top:70%; }*/

}

@media screen and ( max-height: 400px ){
	.bubble { display:none;}
	
	#canvasWrapper {
		height:76vh;
		top: 25px!important;
	}

	.left-button, .right-button {  top: 45px!important;	}
	#inputlabel, #outputlabel {  top: 50px!important;	}

	.navbar {
		height:25px;
		font-size:17px;
		line-height:25px;
	}
		
	.referenceLink{
		height:25px;
		font-size:17px;
		line-height:25px;
	}	
}


#scaleunits {
	display:inline-block;
	padding-right:60px;
	vertical-align:top;
}

@media (max-width: 426px)
{
	
	#canvasWrapper { top: 25px!important; }

	#scaleunits { padding-right:20px; }
	
	.left-button, .right-button {  top: 45px!important;	}
	#inputlabel, #outputlabel {  top: 50px!important;	}
	
	.navbar {
		height:25px;
		font-size:17px;
		line-height:25px;
	}
		
	.referenceLink{
		height:25px;
		font-size:17px;
		line-height:25px;
	}
}


/* ======= SIDEBAR ====== */

/* Position and sizing of burger button */
/*.bm-burger-button {*/
.left-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 20px;
  top: 55px;
}

.right-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 20px;
  top: 55px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  /*background: #373a47;*/
  background: #505359;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

.bm-menu-wrap {
	/*overflow-y:hidden;*/
}

/* General sidebar styles */
.bm-menu {
  background: #505359;
  padding: 0.5em 0em 0.5em 0.5em;
  font-size: 1.05em;
  /*overflow:initial!important;*/
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #505359;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0 0em 0.5em 0.8em;
  height:initial!important;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}


